.textarea__input_17ggy {
    color: var(--input-inverted-color);
    caret-color: var(--input-inverted-caret-color);
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
.textarea__input_17ggy:-webkit-autofill {
    -webkit-text-fill-color: var(--input-inverted-color);
}
.textarea__input_17ggy::placeholder {
    color: var(--input-inverted-placeholder-color);
}
.textarea__hasInnerLabel_17ggy::placeholder {
    color: var(--input-inverted-with-label-placeholder-color);
}
.textarea__clearIcon_17ggy {
    color: var(--color-light-graphic-primary-inverted);
}
.textarea__errorIcon_17ggy {
    background-image: var(--input-inverted-error-icon);
}
.textarea__successIcon_17ggy {
    background-image: var(--input-inverted-success-icon);
}
/* READ-ONLY STATE */
.textarea__input_17ggy:read-only {
    color: var(--input-inverted-read-only-color)
}
.textarea__input_17ggy:read-only:focus::placeholder {
        color: var(--input-inverted-placeholder-color);
    }
/* DISABLED STATE */
.textarea__input_17ggy:disabled {
    color: var(--input-inverted-disabled-color);
    -webkit-text-fill-color: var(--input-inverted-disabled-color);
}
/* FOCUS STATE */
.textarea__input_17ggy:focus::placeholder {
    color: var(--input-inverted-focus-placeholder-color);
}
/* ERROR STATE */
.textarea__error_17ggy {
    color: var(--input-inverted-error-color);
    caret-color: var(--input-inverted-error-color);
}
.textarea__textarea_17ggy {
    color: var(--input-inverted-color);
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
.textarea__textarea_17ggy::placeholder {
    color: var(--input-inverted-placeholder-color);
}
.textarea__hasInnerLabel_17ggy::placeholder {
    color: var(--input-inverted-with-label-placeholder-color);
}
/* DISABLED STATE */
.textarea__textarea_17ggy:disabled {
    color: var(--input-inverted-disabled-color);
    -webkit-text-fill-color: var(--input-inverted-disabled-color);
}
/* READ-ONLY STATE */
.textarea__textarea_17ggy:read-only {
    color: var(--input-inverted-read-only-color);
}
/* FOCUS STATE */
.textarea__textarea_17ggy:focus::placeholder {
    color: var(--input-inverted-focus-placeholder-color);
}
