    /* height */
    --form-control-s-min-height: var(--size-s-height);
    --form-control-m-min-height: var(--size-m-height);
    --form-control-l-min-height: var(--size-l-height);
    --form-control-xl-min-height: var(--size-xl-height);

    /* paddings */
    --form-control-labeled-s-paddings: 24px var(--gap-s) 4px;
    --form-control-labeled-m-paddings: 28px var(--gap-s) 8px;
    --form-control-labeled-l-paddings: 32px var(--gap-s) 12px;
    --form-control-labeled-xl-paddings: 38px var(--gap-m) 14px;

    /* disabled */

    /* font */
}
:root {
    /* min-height */
    --textarea-s-min-height: var(--form-control-s-min-height);
    --textarea-m-min-height: var(--form-control-m-min-height);
    --textarea-l-min-height: var(--form-control-l-min-height);
    --textarea-xl-min-height: var(--form-control-xl-min-height);

    /* padding-top */
    --textarea-s-padding-top: 14px;
    --textarea-m-padding-top: 18px;
    --textarea-l-padding-top: 22px;
    --textarea-xl-padding-top: 24px;

    /* filled margin-top */
    --textarea-s-filled-margin-top: 23px;
    --textarea-m-filled-margin-top: 28px;
    --textarea-l-filled-margin-top: 32px;
    --textarea-xl-filled-margin-top: 38px;

    /* filled min-height */
    --textarea-s-filled-min-height: calc(
        var(--textarea-s-min-height) - var(--textarea-s-filled-margin-top)
    );
    --textarea-m-filled-min-height: calc(
        var(--textarea-m-min-height) - var(--textarea-m-filled-margin-top)
    );
    --textarea-l-filled-min-height: calc(
        var(--textarea-l-min-height) - var(--textarea-l-filled-margin-top)
    );
    --textarea-xl-filled-min-height: calc(
        var(--textarea-xl-min-height) - var(--textarea-xl-filled-margin-top)
    );
}
.textarea__scrollable_1cndn {
    position: relative;
    display: block;
    box-sizing: border-box;
    width: 100%;
}
.textarea__scrollableWrapper_1cndn {
    box-sizing: border-box
}
.textarea__scrollableWrapper_1cndn > * {
        font-family: var(--font-family);
    }
.textarea__textarea_1cndn {
    font-size: 16px;
    line-height: 20px;
    font-weight: 400;

    position: relative;
    display: block;
    width: 100%;
    height: 100%;
    background: none;
    border: none;
    margin: 0;
    outline: none;
    box-sizing: border-box;
    resize: none;
    -webkit-appearance: none
}
.textarea__textarea_1cndn.textarea__s_1cndn {
        padding-top: var(--textarea-s-padding-top);
        padding-bottom: var(--gap-xs);
    }
.textarea__textarea_1cndn.textarea__m_1cndn {
        padding-top: var(--textarea-m-padding-top);
        padding-bottom: var(--gap-xs);
    }
.textarea__textarea_1cndn.textarea__l_1cndn {
        padding-top: var(--textarea-l-padding-top);
        padding-bottom: var(--gap-xs);
    }
.textarea__textarea_1cndn.textarea__xl_1cndn {
        padding-top: var(--textarea-xl-padding-top);
        padding-bottom: var(--gap-xs);
    }
.textarea__textarea_1cndn.textarea__textareaHidden_1cndn {
        position: absolute;
        transform: translateX(-10000px);
    }
.textarea__textarea_1cndn::placeholder {
    transition: opacity 0.2s ease, color 0.2s ease;
}
.textarea__hasInnerLabel_1cndn::placeholder {
    opacity: 0;
}
.textarea__resizeVertical_1cndn {
    resize: vertical;
}
/* SIZES */
.textarea__textarea_1cndn.textarea__s_1cndn, .textarea__scrollable_1cndn.textarea__s_1cndn {
        min-height: var(--textarea-s-min-height);
    }
.textarea__textarea_1cndn.textarea__m_1cndn, .textarea__scrollable_1cndn.textarea__m_1cndn {
        min-height: var(--textarea-m-min-height);
    }
.textarea__textarea_1cndn.textarea__l_1cndn, .textarea__scrollable_1cndn.textarea__l_1cndn {
        min-height: var(--textarea-l-min-height);
    }
.textarea__textarea_1cndn.textarea__xl_1cndn, .textarea__scrollable_1cndn.textarea__xl_1cndn {
        min-height: var(--textarea-xl-min-height);
    }
/* Паддинги взяты из стилей form-control */
.textarea__scrollable_1cndn.textarea__s_1cndn .textarea__textarea_1cndn, .textarea__scrollable_1cndn.textarea__m_1cndn .textarea__textarea_1cndn, .textarea__scrollable_1cndn.textarea__l_1cndn .textarea__textarea_1cndn {
            padding-left: var(--gap-s);
            padding-right: var(--gap-s);
        }
.textarea__scrollable_1cndn.textarea__xl_1cndn .textarea__textarea_1cndn {
            padding-left: var(--gap-m);
            padding-right: var(--gap-m);
        }
/* Паддинги взяты из стилей form-control */
.textarea__scrollableWithLabel_1cndn.textarea__s_1cndn .textarea__textarea_1cndn {
        padding: var(--form-control-labeled-s-paddings);
    }
.textarea__scrollableWithLabel_1cndn.textarea__m_1cndn .textarea__textarea_1cndn {
        padding: var(--form-control-labeled-m-paddings);
    }
.textarea__scrollableWithLabel_1cndn.textarea__l_1cndn .textarea__textarea_1cndn {
        padding: var(--form-control-labeled-l-paddings);
    }
.textarea__scrollableWithLabel_1cndn.textarea__xl_1cndn .textarea__textarea_1cndn {
        padding: var(--form-control-labeled-xl-paddings);
    }
/* DISABLED STATE */
.textarea__textarea_1cndn:disabled {
    cursor: var(--disabled-cursor);
    opacity: 1;
}
/* READ-ONLY STATE */
.textarea__textarea_1cndn:read-only {
    cursor: var(--disabled-cursor);
}
/* FOCUS STATE */
.textarea__textarea_1cndn:focus.textarea__hasInnerLabel_1cndn::placeholder {
    opacity: 1;
}
.textarea__focusVisible_1cndn {
    outline: 2px solid var(--focus-color);
    outline-offset: 2px;
}
/* FILLED STATE */
.textarea__textarea_1cndn.textarea__filled_1cndn.textarea__hasInnerLabel_1cndn {
    padding-top: 0
}
.textarea__textarea_1cndn.textarea__filled_1cndn.textarea__hasInnerLabel_1cndn.textarea__s_1cndn {
        margin-top: var(--textarea-s-filled-margin-top);
        min-height: var(--textarea-s-filled-min-height);
        height: var(--textarea-s-filled-min-height);
    }
.textarea__textarea_1cndn.textarea__filled_1cndn.textarea__hasInnerLabel_1cndn.textarea__m_1cndn {
        margin-top: var(--textarea-m-filled-margin-top);
        min-height: var(--textarea-m-filled-min-height);
        height: var(--textarea-m-filled-min-height);
