.pure-input__component_n5hua {
    font-size: 16px;
    line-height: 20px;
    font-weight: 400;

    display: inline-flex;
    box-sizing: border-box;
    color: var(--input-color);
    background-color: var(--form-control-bg-color);
    border-radius: var(--form-control-border-radius);
    transition: background 0.2s ease, box-shadow 0.2s ease, border 0.2s ease, color 0.2s ease;
    border: none;
    margin: 0;
    padding: var(--form-control-paddings);
    padding-top: 1px;
    border-bottom: var(--form-control-border-bottom);
    outline: none;
    text-overflow: ellipsis;
    -webkit-appearance: none;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
.pure-input__component_n5hua::placeholder {
    text-overflow: ellipsis;
    color: var(--input-placeholder-color);
    transition: color 0.2s ease;
}
.pure-input__block_n5hua {
    width: 100%;
}
/* SIZE */
.pure-input__s_n5hua {
    height: var(--form-control-s-min-height);
}
.pure-input__m_n5hua {
    height: var(--form-control-m-min-height);
}
.pure-input__l_n5hua {
