.pure-input__xl_n5hua {
    height: var(--form-control-xl-min-height);
    padding: var(--form-control-xl-paddings);
    padding-top: 1px;
}
/* DISABLED STATE */
.pure-input__component_n5hua:disabled {
    cursor: var(--disabled-cursor);
    color: var(--input-disabled-color);
    border-bottom: var(--form-control-disabled-border-bottom);
    background-color: var(--form-control-disabled-bg-color);
}
/* FOCUS STATE */
.pure-input__component_n5hua:not(:disabled):focus {
    border-bottom: var(--form-control-focus-border-bottom)
}
.pure-input__component_n5hua:not(:disabled):focus::placeholder {
        color: var(--input-focus-placeholder-color);
    }
.pure-input__focusVisible_n5hua {
    outline: 2px solid var(--focus-color);
