.side-panel__appear_1at2p,
.side-panel__enter_1at2p {
    opacity: 0;
    transform: translateX(15px);
}

.side-panel__appearActive_1at2p,
.side-panel__enterActive_1at2p {
    opacity: 1;
    transform: translateX(0);
    transition: opacity 200ms ease-in, transform 200ms ease-in;
}

.side-panel__exit_1at2p {
    opacity: 1;
    transform: translateX(0);
}

.side-panel__exitActive_1at2p,
.side-panel__exitDone_1at2p {
    opacity: 0;
    transition: opacity 200ms ease-out, transform 200ms ease-out;
    transform: translateX(15px);
}
